<template>
    <div id="member-credit">
        <public-header title="顾客积分明细"></public-header>
        <div class="content">
            <div class="wrap-search wrap-search-notab">
                <van-search placeholder="请输入顾客姓名/UID" v-model="filter.items.keyword" />
            </div>
            <van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
            <van-list
                    v-model="records.loading"
                    :finished="records.finished"
                    @load="onLoad"
                    :offset="100"
                    :immediate-check="false"
                    v-if="!records.empty"
                    class="credit-list"
            >
                <div class="credit-item" v-for="(item, index) in records.data" :key="item.id">
                    <div class="padding-10 flex-lr">
                        <div class="credit-title flex ellipsis">
                            <div class="credit-img">
                                <img :src="item.avatar" alt="">
                            </div>
                            <div class="credit-name">{{item.realname}}</div>
                            <div class="c-gray font-15">UID: {{item.uid}}</div>
                        </div>
                        <div :class="{'c-danger': item.num > 0, 'c-gray': item.num < 0}">{{item.num}}</div>
                    </div>
                    <div class="border-1px-t padding-10">
                        <div class="credit-info flex">
                            <div>变化原因：</div>
                            <div>{{item.remark}}</div>
                        </div>
                        <div class="credit-info flex">
                            <div>操作时间：</div>
                            <div>{{item.createtime_cn}}</div>
                        </div>
                    </div>
                </div>
                <div class="loaded" v-if="records.finished">
                    <div class="loaded-tips">没有更多了</div>
                </div>
            </van-list>
            <div class="no-data" v-else>
                <img src="static/img/order_no_con.png" alt="">
                <p>没有符合条件的数据!</p>
            </div>
            </van-pull-refresh>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
                filter: {
					items: {}
                }
			}
		},
		components: {
			publicHeader,
		},
		methods: {
            onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/member/credit/list'
				});
            },
			onPullDownRefresh(){
            	this.onLoad(true);
            }
		},
		mounted(){
            this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
    #member-credit .content .credit-item{
        background-color: #fff;
        margin-bottom: 10px;
    }
    #member-credit .content .credit-title{
        max-width: 70%;
    }
    #member-credit .content .credit-title .credit-img{
        width: 35px;
        height: 35px;
    }
    #member-credit .content .credit-title .credit-img img{
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
    #member-credit .content .credit-title .credit-name{
        font-weight: bold;
        font-size: 16px;
        padding: 0 10px;
    }
    #member-credit .content .credit-item .credit-info{
        font-size: 14px;
        color: #7a7a7a;
        padding-bottom: 8px;
    }
    #member-credit .content .credit-item .credit-info:last-child{
        padding-bottom: 0;
    }
</style>


